@import "mixins";

html {
    box-sizing: border-box;
}

*,
*::before,
*::after {
    box-sizing: inherit;
}

body {
    margin: 0;
    font-family: sans-serif;
}

img {
    max-width: 100%;
}

a {
    text-decoration: none;
}

.site-container {
    max-width: 1920px;
    margin: 0 auto;
    overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.container {
    max-width: 1210px;
    margin: 0 auto;
    padding: 15px;
}

.is-hidden {
    display: none !important; // stylelint-disable-line declaration-no-important
}

.btn-reset {
    border: none;
    padding: 0;
    background: transparent;
    cursor: pointer;
}

.list-reset {
    list-style: none;
    margin: 0;
    padding: 0;
}

.container {
    margin: 0 auto;
    padding: 0 15px;
    max-width: 1200px;
}

.js-focus-visible :focus:not(.focus-visible) {
    outline: none;
}

.input {
    display: block;
    border-radius: 4px;
    background-color: transparent;
    outline: none;

    &::placeholder {
        color: $color-gray;
    }
}

.textarea::placeholder {
    color: $color-gray;
}

.btn {
    padding: 15px 30px;
    border-radius: 4px;
    background-color: $color-cyan;
    color: $color-white;
    box-shadow: 0px 15px 25px rgba(0, 157, 152, 0.25);
    transition: 0.3s background-color ease-in-out;

    font: $font-xs;

    @include desktop {
        &:hover {
            background-color: $color-seaweed;
        }
    }

    &:focus,
    &:active {
        background-color: $color-bluish-cyan;
    }
}

[data-aos^="fade"][data-aos^="fade"] {
    transition-property: opacity, transform, background-color, padding, color;
}

.title {
    font: $font-l;
    margin: 0;
}

body::-webkit-scrollbar-track {
    background-color: transparent;
    opacity: 0;
}

body::-webkit-scrollbar {
    width: 12px;
    background-color: $color-seaweed;
}

body::-webkit-scrollbar-thumb {
    background-color: $color-bluish-cyan;
    border-radius: 30px;
    border: 3px solid $color-seaweed;
    padding: 0 2px;
}
