.trip {
    position: relative;
    padding: $offset-xl 0;
    background-color: $color-polar;
    z-index: 1;

    &::before {
        content: "";
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 100%;
        height: 100%;
        background: url("../../img/trip-cloud.svg") no-repeat center top/cover;
        z-index: -1;
        user-select: none;
    }

    .trip-slider {
        width: calc(100vw - (100vw - 1210px) / 2);
        padding-bottom: 60px;

        @include tablet {
            width: 100%;
        }
    }

    .swiper-buttons {
        position: absolute;
        top: unset;
        bottom: 50px;
        left: 50%;
        transform: translateX(-50%);
        z-index: 3;
    }

    .swiper-buttons-wrapper {
        position: relative;
        height: 45px;
        width: 130px;

        & .swiper-button:first-child {
            left: 1;
        }

        & .swiper-button:first-child {
            right: 1;
        }
    }

    .swiper-button {
        position: absolute;
        top: 100%;
        width: 45px;
        height: 45px;
        border-radius: 50%;

        background-color: $color-cyan;

        transition: 0.3s background-color ease-in-out;

        &::before,
        &::after {
            display: none;
        }

        @include desktop {
            &:hover {
                background-color: $color-seaweed;
            }
        }

        &:focus,
        &:active {
            background-color: $color-bluish-cyan;
        }
    }

    // .trip__container

    &__container {
        padding: 0 0 0 15px;
    }

    // .trip__top

    &__top {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: $offset-xl;

        img {
            width: 100%;
        }

        @include small-tablet {
            flex-direction: column;
            justify-content: center;
            text-align: center;
        }
    }

    // .trip__group

    &__group {
        margin-right: $offset-m;

        @include small-tablet {
            margin: 0;
        }
    }

    // .trip__kicker

    &__kicker {
        margin-bottom: $offset-m;
        font: $font-xs;
        color: $color-black;
    }

    // .trip__title

    &__title {
        font-size: $font-m;
        color: $color-haiti;
        margin-bottom: $offset-xs;

        @include mobile {
            font-size: 36px;
        }
    }

    // .trip__button

    &__button {
    }

    // .trip__card

    &__card {
    }
}

.trip-card {
    display: inline-block;
    border-radius: 18px;

    // .trip-card__top

    &__top {
        position: relative;
        width: 420px;
        height: 420px;
        margin-bottom: $offset-m;

        img {
            width: 100%;
            height: 100%;
            border-radius: 18px;
            object-fit: cover;
        }

        @include desktop {
            &:hover {
                .trip-card__order {
                    opacity: 1;
                    visibility: visible;
                }
            }
        }

        @include tablet {
            .trip-card__order {
                opacity: 1;
                visibility: visible;
            }
        }

        @include tablet {
            width: 345px;
            height: 345px;
        }

        @include mobile {
            width: 290px;
            height: 290px;
        }
    }

    // .trip-card__order

    &__order {
        position: absolute;
        left: 30px;
        bottom: 30px;
        padding: 15px 30px;
        border-radius: 4px 10px;
        color: $color-white;
        background-color: $color-yellow;
        font: $font-xs;
        opacity: 0;
        visibility: hidden;
        transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out, background-color 0.3s ease-in-out;

        @include desktop {
            &:hover {
                background-color: $color-yellow-hover;
            }
        }

        &:focus,
        &:active {
            background-color: $color-yellow-active;
        }
    }

    // .trip-card__bottom

    &__bottom {
        padding: $offset-m;
        border-radius: 18px;

        @include small-tablet {
            padding: $offset-xs 0;
        }
    }

    // .trip-card__row

    &__row {
        display: flex;
        justify-content: space-between;
        align-items: center;

        &:first-child {
            margin-bottom: $offset-xs;
        }
    }

    // .trip-card__title

    &__title {
        margin: 0;
        font: $font-m;
        color: $color-haiti;
    }

    // .trip-card__rating

    &__rating {
    }

    // .trip-card__subtitle

    &__subtitle {
        display: inline-block;
        padding-bottom: $offset-m;
        font: $font-xs;
        color: $color-gray;
    }

    // .trip-card__group

    &__group {
        display: flex;
        font: $font-xs;
        color: $color-cyan;
    }

    // .trip-card__capacity

    &__capacity {
        display: flex;
        align-items: center;
        margin-right: $offset-l;

        &::before {
            content: "";
            width: 16px;
            height: 16px;
            margin-right: $offset-xs;
            background: url("../../img/trip-users.svg") no-repeat center top/cover;
        }
    }

    // .trip-card__time

    &__time {
        display: flex;
        align-items: center;

        &::before {
            content: "";
            width: 16px;
            height: 16px;
            margin-right: $offset-xs;
            background: url("../../img/trip-calendar.svg") no-repeat center top/cover;
        }
    }

    // .trip-card__price

    &__price {
        font: $font-s;
        color: $color-cyan;
    }
}
