.deal {
    padding: $offset-xl 0;
    background-color: $color-polar;

    // .deal__top

    &__top {
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-wrap: wrap;
        margin-bottom: $offset-xl;

        @include small-tablet {
            flex-direction: column;
            justify-content: center;
            text-align: center;
        }
    }

    // .deal__bottom

    .deal__bottom {
        width: 100%;
        @include small-tablet {
            padding-left: 15px;
        }
    }

    // .deal__title

    &__title {
        color: $color-haiti;
        margin-right: $offset-m;
        margin-bottom: $offset-xs;

        @include small-tablet {
            margin-right: 0;
        }
    }

    // .deal__button

    &__button {
    }

    // .deal__card

    &__card {
    }
}

.deal-card {
    width: 330px;

    @include mobile {
        width: 290px;
    }
    // .deal-card__top

    &__top {
        width: 330px;
        height: 330px;

        img {
            width: 100%;
            height: 100%;
            border-radius: 18px;
            object-fit: cover;
        }

        @include mobile {
            width: 290px;
            height: 290px;
        }
    }

    // .deal-card__bottom

    &__bottom {
        display: flex;
        flex-direction: column;
        padding: $offset-l $offset-l 0 $offset-l;

        width: 330px;
        height: 330px;

        @include mobile {
            width: 270px;
            height: 290px;

            padding: $offset-m $offset-xs 0 $offset-xs;
        }
    }

    // .deal-card__time

    &__time {
        position: relative;
        display: flex;
        align-items: center;
        font: $font-s;
        font-weight: 400;
        color: $color-gray;

        &::before {
            content: "";
            display: block;
            width: 20px;
            height: 20px;
            margin-right: $offset-m;
            background: url("../../img/deal-time-icon.svg") no-repeat center top/cover;
        }
    }

    // .deal-card__subtitle

    &__subtitle {
        max-width: 80%;
        margin-bottom: $offset-m;
        font: $font-s;
        line-height: 34px;
        color: $color-black;

        @include mobile {
            max-width: 100%;
        }
    }

    // .deal-card__capacity

    &__capacity {
        margin-bottom: $offset-l;
        font: $font-xs;
        color: $color-gray;
    }

    // .deal-card__price

    &__price {
        font: $font-m;
        color: $color-cyan;
    }
}
