.contact {
    padding: $offset-xl 0;
    background: url("../../img/contact-background.jpg") no-repeat center top/cover;

    // .contact__container

    &__container {
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-wrap: wrap;

        @include small-tablet {
            justify-content: center;
        }
    }

    // .contact__left

    &__left {
        display: flex;
        flex-direction: column;
        margin-bottom: $offset-m;
        margin-right: $offset-m;

        @include small-tablet {
            align-items: center;
            justify-content: center;
            text-align: center;
            margin-right: 0;
        }
    }

    // .contact__right

    &__right {
        @include small-tablet {
            width: 100%;
        }
    }

    // .contact__title

    &__title {
        margin: 0;
        margin-bottom: $offset-l;
        font: 700 40px/ 100% $font-family;
        color: $color-white;
    }

    // .contact__subtitle

    &__subtitle {
        color: $color-white;
        font: $font-xs;
        transition: 0.3s all ease-in-out;

        &:not(:last-child) {
            margin-bottom: $offset-m;
        }
    }

    // .contact__phone

    &__phone,
    &__email {
        transition: 0.3s all ease-in-out;

        @include desktop {
            &:hover {
                color: $color-polar;
                padding-left: $offset-m;
            }
        }

        &:active,
        &:focus {
            color: rgba($color: $color-white, $alpha: 0.5);
        }
    }

    // .contact__form

    &__form {
        display: flex;
        flex-direction: column;
        width: 100%;

        @include small-tablet {
            justify-content: center;
            align-items: center;
        }
    }
}

.contact-form {
    // .contact-form__item

    &__item {
        display: flex;
        flex-direction: column;
        width: 450px;

        &:not(:last-child) {
            margin-bottom: $offset-m;
        }

        &:last-child {
            max-width: 140px;
        }

        @include small-tablet {
            width: 100%;
        }
    }

    // .contact-form__label

    &__label {
        margin-bottom: $offset-xs;
        font: $font-xs;
        color: $color-white;
    }

    // .contact-form__input

    &__input {
        height: 45px;
        width: 100%;
        padding: 0 $offset-xs;
        border: none;
        font: $font-xs;
        background-color: rgba($color: $color-mercury, $alpha: 0.8);

        &:focus {
            background-color: $color-polar;
            color: $color-haiti;
        }
    }

    // .contact-form__textarea

    &__textarea {
        height: 120px;
        width: 100%;
        padding: $offset-xs;
        margin-bottom: $offset-l;
        border: none;
        border-radius: 4px;
        outline: none;
        resize: none;
        font: $font-xs;
        background-color: rgba($color: $color-mercury, $alpha: 0.8);

        &:focus {
            background-color: $color-polar;
            color: $color-haiti;
        }
    }

    // .contact-form__button

    &__button {
        padding: 15px 50px;
        letter-spacing: 1px;

        @include small-tablet {
            margin: 0 auto;
        }
    }
}
