.footer {
    padding: $offset-l 0;
    background-color: $color-bluish-cyan;
    // .footer__container

    &__container {
        @include small-tablet {
            text-align: center;
        }
    }

    // .footer__top

    &__top {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;

        @include mobile {
            justify-content: center;
        }
    }

    // .footer__list

    &__list {
        padding: 0 $offset-m $offset-xl $offset-m;
    }

    // .footer__title

    &__title {
        display: block;
        margin: 0;
        font: $font-s;
        color: $color-white;
        margin-bottom: $offset-xl;
    }

    // .footer__item

    &__item {
        font: $font-xs;
        color: $color-white;
        &:not(:last-child) {
            margin-bottom: $offset-m;
        }
    }

    // .footer__link

    &__link {
        position: relative;
        font: $font-xs;
        color: $color-white;
        transition: 0.3s all ease-in-out;

        &::before {
            content: "";
            position: absolute;
            top: -5px;
            left: 50%;
            transform: translateX(-50%);
            width: 0;
            height: 1px;
            background-color: $color-white;
            transition: 0.3s width ease-in-out;
        }

        &:focus,
        &:active {
            color: $color-polar;
            opacity: 0.5;
        }

        @include desktop {
            &:hover {
                &::before {
                    width: 100%;
                }
            }
        }
    }

    // .footer__payment

    &__payment {
        max-width: 300px;
        display: inline-block;
        flex-direction: column;
        padding-bottom: $offset-xl;
    }

    // .footer__text

    &__text {
        max-width: 80%;
        font: $font-xs;
        color: $color-white;

        @include small-tablet {
            max-width: 100%;
        }
    }

    // .footer__bottom

    &__bottom {
        text-align: center;
        padding-top: $offset-l;
        border-top: 1px solid $color-white;
    }

    // .footer__copyright

    &__copyright {
        text-transform: uppercase;
        font: $font-xs;
        color: $color-white;
    }
}

.social {
    display: flex;
    align-items: center;
    // .social__item

    &__item {
        &:not(:last-child) {
            margin-right: 25px;
        }
    }

    // .social__link

    &__link {
        display: block;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        transition: 0.3s opacity ease-in-out;

        @include desktop {
            &:hover {
                opacity: 0.5;
            }
        }

        &:focus,
        &:active {
            opacity: 0.5;
        }
    }

    // .social__link--fb

    &__link--fb {
        background-image: url("../../img/facebook-icon.svg");
        width: 11px;
        height: 20px;
    }

    // .social__link--tw

    &__link--tw {
        background-image: url("../../img/twitter-icon.svg");
        width: 19px;
        height: 18px;
    }

    // .social__link--vm

    &__link--vm {
        background-image: url("../../img/vimeo-icon.svg");
        width: 20px;
        height: 17px;
    }

    @include small-tablet {
        justify-content: center;
    }
}
