.about {
    padding: 0 0 $offset-xl 0;
    background-color: $color-polar;

    &__top {
        display: flex;
        justify-content: flex-end;
        padding-bottom: $offset-m;

        @include tablet {
            padding-bottom: $offset-xl;
        }
    }

    @include tablet {
        padding: 0 0 $offset-l 0;
    }
}

.about-form {
    position: relative;
    display: block;
    border-radius: 50px 5px;
    margin-bottom: $offset-xl;
    background-color: $color-white;
    box-shadow: 0px 20px 100px rgba($color: $color-seaweed, $alpha: 0.2);
    z-index: 4;
    margin-top: -$offset-l;
    transform-style: preserve-3d;

    &[data-aos^="fade"][data-aos^="fade"] {
        opacity: 1;
    }

    &::before {
        content: "";
        position: absolute;
        bottom: -30px;
        left: -30px;
        width: 110px;
        height: 110px;
        background: url("../../img/about-dots.svg") no-repeat center top/cover;
        transform: translateZ(-1px);
    }

    // .about-form__row
    &__row {
        display: grid;
        align-items: center;
        grid-template-columns: repeat(4, 1fr);
        grid-template-rows: 1fr;
        grid-column-gap: 60px;
        grid-row-gap: 20px;
        padding: 40px;

        @include tablet {
            grid-template-columns: repeat(2, 1fr);
            grid-template-rows: repeat(2, 1fr);
        }

        @include small-tablet {
            width: 100%;
        }

        @include mobile {
            grid-template-columns: repeat(1, 1fr);
            grid-template-rows: repeat(4, 1fr);
            padding: 30px;
        }
    }

    // .about-form__item

    .choices__list--dropdown {
        margin-top: 10px;
        margin-bottom: 10px;
        border: 1px solid $color-mercury;
        border-radius: 4px;
    }

    .choices__list--dropdown .choices__item--selectable {
        padding: 10px;

        transition: 0.3s background-color ease-in-out, 0.3s padding-left ease-in-out;

        @include desktop {
            &:hover {
                background-color: $color-mercury;
                padding-left: 20px;
            }
        }
    }

    .choices {
        width: 100%;
        margin-bottom: 0;

        &:nth-child(1) .choices__inner {
            background: url("../../img/about-form-location.svg") no-repeat right 20px center/ 16px 20px;
        }

        &:nth-child(2) .choices__inner {
            background: url("../../img/about-form-calendar.svg") no-repeat right 20px center/ 16px 16px;
        }

        &:nth-child(3) .choices__inner {
            background: url("../../img/about-form-passenger.svg") no-repeat right 20px center/ 18px 18px;
        }

        &::after {
            display: none;
        }
    }

    .choices__item {
        font: $font-xs;
        color: $color-gray;
        padding-right: $offset-m;
    }

    .choices__inner {
        border-radius: 4px;
        min-height: auto;
        border: 1px solid $color-mercury;
        border-radius: 4px;
    }

    .choices[data-type*="select-one"] .choices__inner {
        padding: 0;
    }

    .choices__list--single {
        width: 200px;
        padding: 12px 20px;
    }

    .choices__list--dropdown .choices__item--selectable.is-highlighted::after {
        display: none;
    }

    .choices__placeholder {
        font: $font-xs;
        color: $color-gray;
        opacity: 1;
    }

    // .about-form__button

    &__button {
        width: 100%;
        padding: 12px 20px;
    }

    @include tablet {
        width: 100%;
        margin: -$offset-l $offset-xs 0 $offset-xs;
    }
}

.about {
    // .about__image

    &__image {
        position: relative;
        height: auto;
        max-width: 440px;
        margin-right: $offset-xl;
        z-index: 3;

        img {
            border-radius: 10px;
            object-fit: cover;
        }

        &::before {
            content: "";
            position: absolute;
            bottom: -20px;
            right: -30px;
            width: 85px;
            height: 85px;
            border-radius: 8px;
            background-color: $color-yellow;
            object-fit: cover;
            z-index: -1;
        }

        &::after {
            content: "";
            position: absolute;
            top: 50%;
            left: -20%;
            transform: translateY(-50%);
            width: 275px;
            height: 275px;
            background: url("../../img/about-circles.svg") no-repeat center top/cover;
            object-fit: cover;
            z-index: -1;
        }

        @include small-tablet {
            margin-right: 0;
            margin-bottom: $offset-l;
        }
    }

    // .about__row

    &__row {
        display: flex;
        justify-content: space-between;
        align-items: center;

        @include small-tablet {
            flex-direction: column;
            justify-content: center;
        }
    }

    // .about__text

    &__text {
        position: relative;
        max-width: 390px;
        z-index: 2;

        &::before {
            content: "";
            position: absolute;
            top: -20%;
            left: 0;
            transform: translateX(-30%);
            width: 200%;
            height: 200%;
            background: url("../../img/about-illustration.svg") no-repeat center top/ contain;
            z-index: -1;
            user-select: none;

            @include small-tablet {
                width: 150%;
                height: 100%;
                top: 10%;
                left: 50%;
                transform: translateX(-50%);
            }

            @include mobile {
            }
        }

        @include small-tablet {
            max-width: 100%;
        }

        @include small-tablet {
            text-align: center;
        }
    }

    // .about__title

    &__title {
        max-width: 70%;
        color: $color-cyan;

        @include small-tablet {
            max-width: 100%;
        }
    }

    // .about__descr

    &__descr {
        margin-bottom: $offset-l;
        font: $font-xs;
        color: $color-gray;
    }

    // .about__button

    &__button {
    }
}
