.header {
    position: fixed;
    top: 0;
    left: 0;
    padding: 30px 0;
    width: 100%;
    background-color: rgba($color: $color-white, $alpha: 0.9);
    z-index: 99;
    // .header__container

    &__container {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}

.logo {
    display: flex;
    align-items: center;
    text-transform: uppercase;
    font: 700 30px / 110% $font-family;
    color: $color-seaweed;

    &::before {
        content: "";
        display: block;
        margin-right: 5px;
        width: 21px;
        height: 30px;
        background: url("../../img/logo.svg") no-repeat center top/cover;
    }
}

.menu {
    position: relative;
    @include tablet {
        position: fixed;
        top: 0;
        left: -100%;

        width: 100%;
        height: 100%;
        z-index: 99;

        background-color: $color-white;
        opacity: 0;
        transition: 0.3s all ease-in;
    }

    &--active {
        left: 0;

        display: block;

        opacity: 1;
    }

    // .menu__line

    &__line {
        position: absolute;
        left: 0;
        bottom: -5px;
        opacity: 0;

        width: 20px;
        height: 2px;
        border-radius: 100px;

        background-color: $color-cyan;
        transition: 0.5s all ease-in-out;

        @include tablet {
            display: none;
        }
    }

    // .menu__list

    &__list {
        display: flex;
        text-align: center;

        @include tablet {
            width: 100%;
            height: 100%;
            flex-direction: column;
            align-items: center;
            justify-content: space-evenly;
        }
    }

    // .menu__item

    &__item {
        &:not(:last-child) {
            margin-right: 30px;
        }

        @include tablet {
            &:not(:last-child) {
                margin-right: 0;
            }
        }
    }

    // .menu__link

    &__link {
        font: $font-xs;
        color: $color-black;
        transition: 0.3s color ease-in-out;

        @include tablet {
            font: $font-l;
            color: $color-seaweed;
            font-weight: 700;
        }

        &:active,
        &:focus {
            color: $color-bluish-cyan;
        }
    }
}

.burger {
    @include burger;

    display: none;

    @include tablet {
        z-index: 99;
        display: block;
    }
}
