.hero {
    position: relative;
    height: 1080px;
    max-height: 100%;

    background-color: $color-polar;
    z-index: 1;
    overflow: hidden;

    &::before {
        content: "";
        position: absolute;
        top: 0;
        right: 0;
        background: url("../../img/hero-top-image.svg") no-repeat center top/cover;
        width: 1030px;
        height: 600px;
        object-fit: cover;
        z-index: -1;
    }

    &::after {
        content: "";
        position: absolute;
        bottom: 0;
        right: 0;
        background: url("../../img/hero-bottom-image.svg") no-repeat center top/cover;
        width: 100%;
        height: 450px;
        object-fit: cover;
        z-index: -1;
    }

    // .hero__container

    &__container {
        position: relative;
        height: 100%;
    }

    // .hero__title

    &__title {
        position: absolute;
        top: 30%;
        left: 0;
        display: block;
        text-align: center;
        max-width: 50%;
        padding: 0 5px;

        font: 900 75px / 80px "Poppins", sans-serif;
        color: $color-polar;
        letter-spacing: 3px;
        text-shadow: rgb(8, 60, 76) 2px 0px 0px, rgb(8, 60, 76) 1.75517px 0.958851px 0px,
            rgb(8, 60, 76) 1.0806px 1.68294px 0px, rgb(8, 60, 76) 0.141474px 1.99499px 0px,
            rgb(8, 60, 76) -0.832294px 1.81859px 0px, rgb(8, 60, 76) -1.60229px 1.19694px 0px,
            rgb(8, 60, 76) -1.97998px 0.28224px 0px, rgb(8, 60, 76) -1.87291px -0.701566px 0px,
            rgb(8, 60, 76) -1.30729px -1.5136px 0px, rgb(8, 60, 76) -0.421592px -1.95506px 0px,
            rgb(8, 60, 76) 0.567324px -1.91785px 0px, rgb(8, 60, 76) 1.41734px -1.41108px 0px,
            rgb(8, 60, 76) 1.92034px -0.558831px 0px;
        z-index: 99;

        @include tablet {
            font-size: 70px;
            max-width: 62%;
        }

        @include small-tablet {
            font-size: 65px;
            max-width: 80%;
        }

        @include mobile {
            font-size: 55px;
            max-width: 100%;
        }
    }
}
