@mixin burger {
    position: relative;
    border: none;
    padding: 0;
    width: $burger-width;
    height: $burger-height;
    color: $color-seaweed;
    background-color: transparent;
    cursor: pointer;

    &::before,
    &::after {
        content: "";
        position: absolute;
        left: 0;
        width: 100%;
        height: $burger-line-height;
        background-color: currentColor;
        transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
        border-radius: 20px;
    }

    &::before {
        top: 0;
    }

    &::after {
        top: calc(100% - $burger-line-height);
    }

    &__line {
        position: absolute;
        left: 0;
        top: 50%;
        width: 100%;
        height: $burger-line-height;
        background-color: currentColor;
        transform: translateY(-50%);
        transition: transform 0.3s ease-in-out;
        border-radius: 20px;
    }

    &--active {
        color: $color-bluish-cyan;
        &::before {
            top: 50%;
            transform: rotate(45deg);
            transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
        }

        &::after {
            top: 50%;
            transform: rotate(-45deg);
            transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
        }

        .burger__line {
            transform: scale(0);
            transition: transform 0.3s ease-in-out;
        }
    }
}

.body--hidden {
    overflow: hidden;
}
