.modal__close {
    position: absolute;
    right: 10px;
    top: 10px;
    border: none;
    padding: 0;
    background-color: transparent;
    cursor: pointer;
    font: $font-xs;
    font-weight: bold;
    color: $color-red;
    text-transform: uppercase;
}

.modal__form {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.modal__container {
    background-color: $color-light-yellow;
    @include mobile {
        padding: 12px;
    }
}

.modal-form {
    // .contact-form__item

    &__title {
        margin-top: $offset-m;
        max-width: 50%;
        font: $font-m;
        color: $color-haiti;
        text-align: center;

        @include small-tablet {
            max-width: 100%;
        }

        @include mobile {
            font-size: 30px;
        }
    }

    &__item {
        display: block;
        width: 80%;
        &:not(:last-child) {
            margin-bottom: $offset-m;
        }

        @include mobile {
            width: 100%;
        }
    }

    // .modal-form__input

    &__input {
        height: 45px;
        width: 100%;
        padding: 0 $offset-xs;
        border: none;
        font: $font-xs;
        background-color: $color-polar;

        &:focus {
            background-color: $color-mercury;
        }
    }

    // .modal-form__textarea

    &__textarea {
        height: 120px;
        width: 100%;
        padding: $offset-xs;
        margin-bottom: $offset-m;
        border: none;
        border-radius: 4px;
        outline: none;
        resize: none;
        font: $font-xs;
        background-color: $color-polar;

        &:focus {
            background-color: $color-mercury;
        }
    }

    // .modal-form__button

    &__button {
        width: 240px;
        letter-spacing: 1px;
        background-color: $color-haiti;

        @include small-tablet {
            margin: 0 auto;
        }
    }
}
